<template>
  <div class="loginPage">
    <h1>{{msg}}</h1>
    <div class="input">
      <input type="text" v-model="info.id" placeholder="ID">
      <br/>
      <input type="text" v-model="info.pw" placeholder="PW">
    </div>
    <br>
    <button v-on:click="signIn(info.id, info.pw)">로그인</button>
    <button v-on:click="signUp">회원가입</button>
  </div>
</template>

<script>

export default {
  name: 'loginPage',
  /* 데이터 정의 */
  data () {
    return {
      msg: 'This is Login Page',
      info: {}
    }
  },
  /* 펑션 정의 */
  methods: {
    signIn: function (id, pw) {
      alert(`input id : ${id}\ninput pw : ${pw}`)
    },
    signUp: function () {
      // 라우터에 라우팅 페이지가 등록되어 있으면 아래 구문으로 해당페이지로 넘어갈 수 있다
      this.$router.push('signUpForm')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
