<template>
    <div id="firebaseData">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>USER INFO</h4>
            </li>
            <li v-for="user in users" v-bind:key="user.firebaseId" class="collection-item">
                <div class="chip">{{user.auth}}</div>
                {{user.userId}} : {{user.name}}
            </li>
        </ul>
    </div>
</template>

<script>
import firebaseDB from './firebaseInit'
export default {
  name: 'firebaseData',
  data () {
    return {
      users: []
    }
  },
  created () {
    firebaseDB.collection('user').get().then(
      querySnapshot => {
        querySnapshot.forEach(result => {
          console.log(result.data())
          const data = {
            'firebaseId': result.id,
            'userId': result.data().user_id,
            'name': result.data().name,
            'auth': result.data().auth
          }
          this.users.push(data)
        })
      })
  }
}
</script>



